
import React, {useEffect} from 'react';
import {Link} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import {getPositionList} from "../../../redux/action/career";

import Accordion from "../../layout/Accordion/Accordion";

import styles from "./CareerPagePositionList.module.scss"
import {careerPagePath} from "../../../router/path";
import {scrollTop} from "../../../globalFunctions";

function CareerPagePositionList() {
    const dispatch = useDispatch()
    const {data} = useSelector(state => state.career)

    useEffect(() => {
        dispatch(getPositionList())
        // eslint-disable-next-line react-hooks/exhaustive-deps
    },[])


    const onClickJoin = () => scrollTop()


    return (
        <section>
            {data?.length && data.map(({title,description,id},index) => {
                const descriptionContent = <>
                    <p dangerouslySetInnerHTML={{__html: description}}/>
                    <Link
                        onClick={onClickJoin}
                        to={`${careerPagePath}/${id}`}
                        className={styles['careerPagePositionList__joinLink']}
                    >Join us</Link>
                </>

                return (
                    <Accordion
                        key={index}
                        index={index}
                        title={title}
                        contentText={descriptionContent}
                    />
                )
            })}
        </section>
    );
}

export default CareerPagePositionList;