import {
    CLEAR_SELECTED_PROFESSION,
    GET_CAREER_LOADING_START,
    GET_CAREER_SUCCESS,
    SET_SELECTED_PROFESSION
} from "../types";
import {careerUrl, fetchRequest} from "./fetchTools";


export const getPositionList = (id) => async (dispatch) => {

    const idState = id ? `/${id}` : ''

    dispatch({type: GET_CAREER_LOADING_START})
    try {
        const fetchData = await fetchRequest(careerUrl + idState)
        if (id) {
            dispatch({type: SET_SELECTED_PROFESSION, payload: fetchData.data})
        } else {
            dispatch({type: GET_CAREER_SUCCESS, payload: fetchData.data})
        }
    } catch (err) {
        throw new Error(err)
    }
}


export const clearSelectedProfession = () => dispatch => dispatch({type: CLEAR_SELECTED_PROFESSION})